@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@200;300;400;500;600;700;800&display=swap);
* {
	padding: 0;
	margin: 0;
	font-family: "Dosis", sans-serif;
}

.index {
	background-color: #262626;
}

.home-container {
	background-image: url(/Music-Blog/static/media/bg-img.c965b427.png);
	background-repeat: repeat;
	background-size: contain;
	margin-top: -1px;
}

.Nav-container {
	height: 125px;
}

.Nav-bar-top {
	display: flex;
	justify-content: space-between;
	padding: 0 15%;
	align-items: center;
	height: 40px;
	background-color: #2c2c2c;
}

.Nav-welcome,
.nav-top-btn-li,
.nav-top-lr,
.Nav-main-links {
	color: #686868;
}

.Nav-main-links:hover {
	cursor: pointer;
	color: #ea4839;
}

.nav-top-btn {
	display: flex;
	width: 19%;
	justify-content: space-between;
	margin-right: 12%;
}

.nav-top-login,
.nav-top-register {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.nav-top-login:hover,
.nav-top-register:hover,
.nav-top-bnt-li:hover {
	cursor: pointer;
	color: #ea4839;
}

.nav-top-register {
	margin-left: 10px;
}

.nav-top-login span,
.nav-top-register span {
	margin-left: 5px;
}

.nav-top-btn li {
	width: 20px;
	display: flex;
	align-items: center;
	padding: 0 2px;
	list-style: none;
}

.nav-top-btn-li {
	display: flex;
}

.nav-top-bnt-li {
	padding: 0 5px;
}

.Nav-main {
	display: flex;
	background-color: #252525;
	justify-content: space-between;
	padding: 0 15%;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}

.logo img {
	height: 5rem;
	cursor: pointer;
}

.nav-main-ul {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 55%;
}

.nav-main-ul li {
	list-style: none;
}

.Header-main {
	width: 70%;
	padding-top: 100px;
	padding-bottom: 20px;
	margin: 0 auto;
	padding: auto 20px;
	flex-wrap: wrap;
}
.Header-box {
	height: 140px;
	width: 140px;
	background-color: cadetblue;
	border: 1px solid grey;
}

.Header-slider-item {
	height: 200px;
	text-align: center;
}

.Header-slider-item h2,
.Header-slider-item p {
	color: white;
}

.Header-slider-item h2 {
	font-size: 40px;
}

.Header-slider-item p {
	width: 80%;
	margin: 10px auto;
}

.header-slider-btn {
	margin-top: 25px;
	padding: 15px 40px;
	cursor: pointer;
	border-radius: 0;
	background-color: rgba(255, 255, 255, 0.25);
	border: 2px solid white;
	color: white;
}

.header-slider-btn:hover {
	background-color: rgba(255, 255, 255, 0.4);
}

.header-feature {
	width: 80%;
	background-color: #1e1e1e;
	margin: 0 auto;
	text-align: center;
	transform: translateY(35%);
}

.header-feature-heading {
	padding: 50px 0;
}

.header-txt-red {
	color: #ea4839;
}

.HeaderFeatureText,
.header-feat-text {
	color: #686868;
}

.header-feat-head,
.HeaderFeatureHeading {
	color: white;
}

.HeaderFeatureIcon {
	font-size: 2rem;
	color: #ea4839;
}

.header-feature-heading {
	margin: 10px 0;
	font-weight: 500;
}

.HeaderFeatureItem {
	display: flex;
	flex-direction: column;
	width: 200px;
	height: 200px;
}

.flex-box {
	display: flex;
	justify-content: space-evenly;
}

.main {
	transform: translateY(7%);
}

.MainWide-h2 {
	color: white;
}

.MainWide-h2 {
	font-size: 1rem;
}

.main-grey-p {
	margin-top: 10px;
}

.flex-align-cen {
	display: flex;
	align-items: center;
}

.main-btm-head {
	margin-bottom: 20px;
}

/* Styles for Main Bottom */
.main-bottom {
	justify-content: center;
	display: flex;
	padding-top: 60px;
	width: 80%;
	margin: 0 auto;
	background-color: #191919;
	padding-bottom: 60px;
}

.main-events {
	width: 38%;
	display: flex;
	flex-direction: column;
}

.main-feat-head {
	color: white;
}

.main-txt-red {
	color: #ea4839;
}

.main-events,
.main-btm-blog {
	padding: 0 5px;
}

.main-btm-blog {
	width: 44%;
}

.main-grey-text {
	font-size: 0.8rem;
}

.main-tall-events {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
}

.main-grey-text,
.main-grey-i,
.main-grey-p,
.mac-p {
	color: #686868;
}

.main-grey-i {
	font-size: 0.625rem;
}

.main-grey-p {
	font-size: 0.8rem;
}

.main-grey-i-container {
	margin-top: 5px;
}

.MainBlogCardContainer {
	display: flex;
	justify-content: space-around;
	align-items: center;
	height: auto;
}

.main-blog-container {
	height: 600px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.blog-card-contents {
	margin-left: 10px;
}

/* Styles for main-before-footer */
.main-before-footer {
	background-color: #1e1e1e;
	width: 80%;
	margin: 0 auto;
	display: grid;
	justify-items: center;
	grid-template-columns: repeat(2, 1fr);
	grid-template-rows: repeat(2, 1fr);
	grid-column-gap: 0px;
	grid-row-gap: 80px;
	padding-top: 80px;
	padding-bottom: 140px;
}

.mbf-card {
	width: 75%;
}

.mbf-card-cont {
	display: flex;
	justify-content: space-between;
}

/* styles for MainArtistCardContainer = mac */
.mac-view {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 40px;
}

.mac-view-btn,
.mac-view-text {
	height: 48px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.mac-view-btn {
	background-color: #282828;
	width: 25%;
	color: #747474;
	cursor: pointer;
}

.mac-view-text {
	background-color: #131313;
	width: 75%;
	align-items: baseline;
	padding-left: 5px;
}

.mac-h4 {
	font-size: 0.7rem;
	color: white;
}

.mac-p {
	font-size: 0.7rem;
}

/* styles for ArtistCardHead */
.ArtistCardHead {
	color: white;
	height: 3rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.ArtistCardHeadBtn {
	margin-top: 0;
}

.RedBtn,
.GreyBtn,
.GreenBtn {
	margin-top: 15px;
	padding: 8px 25px;
	cursor: pointer;
	border-radius: 0;
	background-color: #f14a34;
	border: none;
	color: white;
}

.RedBtnSmall,
.GreyBtnSmall,
.GreenBtnSmall {
	padding: 5px 15px;
	cursor: pointer;
	border-radius: 0;
	background-color: #f14a34;
	border: none;
	color: white;
}

.RedBtn:hover,
.GreenBtn:hover,
.GreyBtn:hover {
	box-shadow: inset 0 0 0 10em rgba(255, 255, 255, 0.3);
}

.GreyBtn {
	background-color: #666060;
}
.GreenBtn {
	background-color: #839836;
}

.ReadMore {
	background-color: transparent;
	color: #f14a34;
	cursor: pointer;
	border: none;
}

.ReadMore:hover {
	color: #f88373;
}

footer {
	padding-top: 60px;
	padding-bottom: 1px;
	width: 80%;
	margin: 0 auto;
	background-color: #191919;
	position: relative;
	margin-top: 80px;
}

.ft-gotop-btn {
	background: transparent;
	border: none;
	padding: 10px 10px;
	color: #444;
}

.ft-flex-box {
	display: flex;
	justify-content: center;
	position: relative;
}

.ft-gotop-btn {
	position: absolute;
	right: 0;
	top: 0;
}

.ft-cont {
	width: 20%;
}

.ft-span-i {
	color: white;
	margin-top: 10px;
}

.ft-vertical-line {
	height: 340px;
	background-color: #252525;
	width: 0px;
	border: 1px solid #252525;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-content: center;
	margin: 0 10px;
	transform: translateY(0px);
}

.ft-hr {
	width: 80%;
	margin: 0 auto;
	background-color: #252525;
	border: 1px solid #252525;
}

.ft-cont p {
	font-size: 0.6875rem;
	color: #686868;
}

.ft-cont-p-sec {
	margin-top: 20px;
}

.ft-cont-white {
	color: white;
}

.ft-cont-red {
	color: #ea4839;
}

.ft-cont-red,
.ft-cont-white {
	font-size: 0.6875rem;
}

.ft-quick-links {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	height: 150px;
	margin-top: 15px;
}

.ft-quick-links span {
	color: #686868;
	margin-top: 8px;
	font-size: 0.75rem;
	cursor: pointer;
}

.ft-quick-links span:hover {
	color: #ea4839;
}

.ft-cont-head-span {
	margin-left: 10px;
}

.submit-email {
	height: 50px;
	display: flex;
	align-items: center;
	width: 90%;
}

.submit-email-input {
	background-color: #282828;
	border: none;
	color: #686868;
	height: 30px;
	padding: 0 10px;
	width: 100%;
}

.submit-email-btn {
	border: none;
	border-radius: 0;
	background-color: #ef4933;
	color: white;
	font-weight: 500;
	height: 30px;
	padding: 0 5px;
	text-transform: uppercase;
	cursor: pointer;
}

.submit-email-btn:hover {
	box-shadow: inset 0 0 0 10em rgba(255, 255, 255, 0.3);
}

.ft-btm {
	display: flex;
	justify-content: space-between;
	margin-top: 30px;
	width: 80%;
	margin: 30px auto;
}

.ft-btm-copyright,
.ft-btm-love {
	font-size: 10px;
}

.ft-btm-copyright,
.ft-btm-love span {
	color: #686868;
}

.ft-btm-love i {
	font-size: 0.8rem;
	color: #ea4839;
}

.ft-btm-love span {
	margin-left: 10px;
}

.ft-btm-social {
	width: 30%;
	display: flex;
	justify-content: space-around;
}

.ft-btm-social-i {
	height: 22px;
	border-radius: 50%;
	background-color: #777;
	color: #1b1b1b;
	width: 22px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.ft-btm-social-i i {
	font-size: 0.75rem;
	cursor: pointer;
}

.ft-btm-social-i:hover {
	background-color: #ef4933;
}

